#bracket {
  position: relative;
  width: 1270px;
  height: 820px;
  overflow: auto;
  margin: 0 auto;
}

[id*='game'] {
  position: absolute;
}

[id*='game'] button {
  display: block;
  box-sizing: border-box;
  width: 130px;
  height: 20px;
  font-size: 0.8rem;
  border: 0;
  border-bottom: 2px solid var(--grey);;
  border-radius: 0px !important;
  background-color: var(--light);
}

[id*='game'] button:disabled {
  cursor: not-allowed !important;
  border-bottom-color: var(--silver);
}

[id*='game'] button.win {
  border-bottom: 2px solid var(--primary);
}

[id*='game'] button.loss {
  border-bottom-color: var(--silver);
  text-decoration: line-through;
  color: var(--silver);
}

/* Round 1 */
#game31, #game32, #game33, #game34, #game35, #game36, #game37, #game38, 
#game39, #game40, #game41, #game42, #game43, #game44, #game45, #game46 { left: 0px; }
